.blog-actual {
  padding-top: 0;
  padding-bottom: 72px;

  @include tablet {
    padding-top: 0;
    padding-bottom: 56px;
  }

  @include medium-tablet {
    padding-top: 24px;
  }

  @include mobile {
    padding-bottom: 48px;
  }

  &__title {
    margin-bottom: 48px;

    @include medium-tablet {
      margin-bottom: 24px;
    }

    @include mobile {
      margin-bottom: 12px;
    }
  }

  .blog__show-more {
    display: block;
    margin-top: 32px;
    margin-right: auto;
    margin-left: auto;

    @include small-tablet {
      width: 100%;
    }

    @include mobile {
      margin-top: 12px;
    }
  }

  .posts {
    max-width: 100%;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 0;

    @include small-tablet {
      margin-bottom: 0;
    }
  }

  .blog-post {
    padding: 24px;
    border-bottom: solid 1px var(--grey-400);

    &:nth-child(n + 6) {
      display: none;
    }

    &.is-visible {
      display: block;
    }

    @include mobile {
      margin-bottom: 24px;
    }
  }

  .blog-post__title {
    max-width: 608px;
    margin-bottom: 12px;
  }

  .blog-post__link {
    color: var(--base-black);
    transition: color 0.3s ease-in;

    &:hover {
      color: var(--cyan-400);
    }
  }

  .blog-post__date {
    color: var(--grey-500);
  }

  .blog-post__tags {
    display: flex;
    flex-wrap: wrap;
    margin-top: 24px;
  }

  .blog-post__tag {
    display: inline-flex;
    padding: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 121%;
    min-height: 36px;
    color: var(--base-black);
    border-radius: 12px;
    background-color: var(--grey-200);
    transition: color 0.3s ease-in, background-color 0.3s ease-in;

    &:hover {
      color: var(--base-white);
      background-color: var(--cyan-400);
    }
  }

  .blog-post__tag {
    margin-bottom: 8px;
  }

  .blog-post__tag:not(:last-child) {
    margin-right: 8px;
  }

  &-tabs {
    display: flex;

    @include tablet {
      flex-direction: column;
    }
  }

  .tabs__content {
    width: 100%;
  }

  .tabs__panel {
    display: none;
  }

  .tabs__nav {
    width: 196px;
    flex-shrink: 0;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-right: 24px;

    @include tablet {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid var(--grey-200);
      overflow-x: auto;
      white-space: nowrap;
      scrollbar-width: none;

      /*mozilla*/
      &::-webkit-scrollbar {
        display: none;
        /*chrome*/
      }
    }
    @include mobile {
      margin-bottom: 12px;
    }
  }

  .tabs__nav-title {
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 24px;
    line-height: 121.9%;
    color: var(--base-grey);

    @include tablet {
      margin-bottom: 0;
      margin-right: 32px;
      font-size: 20px;
    }

    @include mobile {
      font-weight: 700;
      font-size: 16px;
      margin-right: 24px;
    }
  }

  .tabs__nav-item:not(:last-child) {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 0;
      margin-right: 24px;
    }

    @include mobile {
      margin-right: 16px;
    }
  }

  .tabs__nav-btn {
    font-weight: 500;
    font-size: 20px;
    line-height: 121.9%;
    color: var(--grey-400);
    background-color: transparent;
    border-bottom: 2px solid var(--base-white);
    transition: color 0.5s ease, border-bottom 0.5s ease;

    &:hover {
      color: var(--cyan-400);
    }

    @include tablet {
      padding: 16px 0;
      font-weight: 600;
      font-size: 16px;
    }
  }

  .tabs__panel--active {
    display: block;
  }

  .tabs__nav-btn--active {
    color: var(--base-grey);
    border-bottom: 2px solid var(--cyan-400);

    &:hover {
      color: var(--base-grey);
    }
  }
}
