.contacts {
  padding-top: 72px;
  background-color: var(--base-black);

  @include mobile {
    padding-bottom: 12px;
  }

  &__title {
    color: var(--base-white);
    margin-bottom: 48px;
    @include small-tablet {
      margin-bottom: 32px;
    }
  }

  &__row {
    display: flex;
    height: 700px;

    @media (max-width: 1070px) {
      height: 510px;
    }

    @media (max-width: 850px) {
      height: 460px;
    }

    @include small-tablet {
      height: auto;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__map {
    margin-right: auto;
    margin-left: auto;
    width: 500px;
    height: auto;

    @media (min-width: 768px) {
      display: none;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__column {
    max-width: 304px;
    z-index: 10;

    &:nth-child(2) {
      position: relative;

      &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 963px;
        height: 701px;
        background-image: url('../img/page-contacts/map.jpg');
        object-fit: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        z-index: -1;

        @media (max-width: 1070px) {
          width: 700px;
          height: 510px;
        }

        @media (max-width: 850px) {
          width: 630px;
          height: 460px;
        }

        @include small-tablet {
          display: none;
        }
      }
    }

    &:not(:last-child) {
      margin-right: 24px;
    }

    &-group:not(:last-child) {
      margin-bottom: 56px;
    }

    &-title {
      font-weight: 600;
      font-size: 16px;
      line-height: 121.9%;
      color: var(--grey-300);
      margin-bottom: 12px;
    }

    &-item {
      font-weight: 400;
      font-size: 18px;
      line-height: 156%;
      color: var(--base-white);

      a {
        cursor: pointer;
        color: var(--base-white);
        transition: color 0.3s ease-in;

        &:hover {
          color: var(--cyan-400);
        }
      }
    }

    &-item--cyan a {
      color: var(--cyan-400);
      transition: 0.3s;
      position: relative;
      width: fit-content;
      cursor: pointer;
      text-decoration: none;

      &::after {
        display: block;
        position: absolute;
        left: 0;
        width: 0;
        height: 2px;
        background-color: var(--cyan-400);
        content: "";
        transition: width 0.3s ease-out;
      }

      &:hover {
        &:after {
          width: 100%;
        }
      }
    }

    &-link {
    }
  }
}
