.heading {
  margin-top: 48px;
  margin-bottom: 72px;

  @include tablet {
    margin-bottom: 42px;
  }

  @include medium-tablet {
    margin-bottom: 32px;
  }

  @include small-tablet {
    margin-top: 32px;
    margin-bottom: 24px;
  }

  @include mobile {
    margin-top: 24px;
    margin-bottom: 12px;
  }

  &__container {
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 240px;
    padding: 30px 72px;
    border-radius: 24px;
    background-color: var(--grey-100);
    background-image: url('../img/page-products/heading-img.png');
    background-size: 35% auto;
    background-repeat: no-repeat;
    background-position: right;

    @include tablet {
      padding: 40px 45px;
      min-height: 216px;
    }

    @media (max-width: 950px) {
      min-height: auto;
    }

    @include small-tablet {
      padding: 35px;
      border-radius: 12px;
    }
    @include mobile {
      padding: 30px 24px;
      background-image: none;
    }
  }

  &__content {
    max-width: 490px;

    @media (max-width: 950px) {
      max-width: 370px;
    }
    @include small-tablet {
      max-width: 330px;
    }
    @media (max-width: 650px) {
      max-width: 300px;
    }
    @include mobile {
      text-align: center;
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    @include tablet {
      font-size: 22px;
    }
    @media (max-width: 950px) {
      font-size: 18px;
    }
    @include small-tablet {
      font-size: 16px;
    }
    @include mobile {
      text-align: center;
    }
  }

  &__tags {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 12px;

    @include mobile {
      justify-content: center;
      margin-bottom: 8px;
    }
  }

  &__tag {
    display: inline-flex;
    padding: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 121%;
    min-height: 36px;
    color: var(--base-black);
    border-radius: 12px;
    background-color: var(--grey-200);
    transition: color 0.3s ease-in, background-color 0.3s ease-in;

    &:hover {
      color: var(--base-white);
      background-color: var(--cyan-400);
    }
  }

  &__tag {
    margin-bottom: 8px;
  }

  &__tag:not(:last-child) {
    margin-right: 8px;
  }
}
