.product-numb {
  padding-top: 72px;
  padding-bottom: 48px;

  @include medium-tablet {
    padding-top: 62px;
    padding-bottom: 42px;
  }

  @include small-tablet {
    padding-top: 52px;
    padding-bottom: 32px;
  }

  @include mobile {
    padding-top: 32px;
    padding-bottom: 24px;
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 32px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    &-title {
      font-weight: 600;
      font-size: 48px;
      line-height: 121.9%;
      color: var(--base-grey);

      @include medium-tablet {
        text-align: center;
      }
    }

    hr {
      width: 96px;
      margin: 12px 0;
      padding: 0;
      height: 0;
      border: none;
      border-top: 2px solid var(--cyan-400);

      @include medium-tablet {
        margin: 12px auto 12px auto;
      }

      @include small-tablet {
        margin: 6px auto 6px auto;
      }
    }

    &-descr {
      color: var(--grey-700);

      @include medium-tablet {
        text-align: center;
      }
    }
  }
}
