.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.92);

  @include medium-tablet {
    height: 74px;
  }

  @include small-tablet {
    padding-top: 12px;
    padding-bottom: 12px;
    height: auto;
  }

  &-contacts {
    background-color: var(--base-black);
  }

  .logo {
    width: 79px;
    height: 48px;

    @include medium-tablet {
      width: 65px;
      height: 40px;
    }

    @include small-tablet {
      width: 52px;
      height: 32px;
      margin-right: 24px;
      margin-left: auto;
    }
  }

  &__btns {
    display: flex;

    @include small-tablet {
      display: none;
    }
  }

  .language__btn {
    margin-left: 24px;
    width: 44px;
    height: 44px;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 44px;
    color: var(--base-white);
    background: rgba(255, 255, 255, 0.2);
    border-radius: 40px;

    @include medium-tablet {
      margin-left: 10px;
    }
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include small-tablet {
    justify-content: right;
  }
}

.menu {
  display: flex;
  align-items: center;

  @include small-tablet {
    display: none;
  }
}

.search__wrapper {
  width: 80px;
  height: 80px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-right: 24px;

  @include tablet {
    margin-right: 5px;
  }

  @include medium-tablet {
    display: none;
  }
}

._icon-SearchLarge, ._icon-Close {
  font-size: 24px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  transition: color 0.3s ease-in;

  &:hover {
    color: var(--cyan-400);
  }
}

// Меню

.nav {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    padding: 0 8px;

    a {
      font-weight: 600;
      font-size: 16px;
      color: var(--grey-100);
      line-height: 80px;
      transition: color 0.3s ease-in;

      @include medium-tablet {
        font-size: 15px;
        line-height: 74px;
      }

      &:hover {
        color: var(--cyan-400);
      }
    }
  }

  &__item:not(:last-child) {
    margin-right: 24px;

    @include medium-tablet {
      margin-right: 5px;
    }
  }

  &__btn {
    border: none;
    font-weight: 600;
    font-size: 16px;
    line-height: 80px;
    color: var(--grey-100);
    display: inline-flex;
    align-items: center;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.3s ease-in;

    i {
      font-size: 16px;
      line-height: 80px;
      margin-left: 8px;
      transition: transform 0.3s ease-in-out, color 0.3s ease-in;
    }

    @include medium-tablet {
      font-size: 15px;
      line-height: 74px;

      i {
        font-size: 15px;
        line-height: 74px;
        margin-left: 6px;
      }
    }
  }

  &__btn:hover {
    color: var(--cyan-400);

    i {
      color: var(--cyan-400);
    }
  }

  &__btn--active {
    color: var(--cyan-400);
    i {
      transform: rotate(180deg);
      color: var(--cyan-400);
    }
  }

  .dropdown {
    position: absolute;
    left: 0;
    width: 100%;
    padding-top: 48px;
    padding-bottom: 48px;
    background-color: var(--base-grey);
    z-index: 1002;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  .dropdown--active {
    opacity: 1;
    visibility: visible;
  }

  .dropdown__content {
    max-width: 1064px;
    margin: 0 auto;

    @media (max-width: 1112px) {
      margin: 0 24px;
    }
  }

  .products-solution-tabs {
    display: flex;

    @include tablet {
      flex-direction: column;
    }
  }

  .tabs__panel {
    display: none;
  }

  .tabs__nav {
    width: 196px;
    flex-shrink: 0;
    list-style: none;
    margin: 0;
    padding: 0;
    margin-right: 24px;
    @include tablet {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid var(--grey-200);
      overflow-x: auto;
      white-space: nowrap;
      scrollbar-width: none;

      /*mozilla*/
      &::-webkit-scrollbar {
        display: none;
        /*chrome*/
      }
    }
  }

  .tabs__nav-title {
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 24px;
    line-height: 121.9%;
    color: var(--grey-100);

    @include tablet {
      margin-bottom: 0;
      margin-right: 32px;
      font-size: 20px;
    }

    @include mobile {
      font-weight: 700;
      font-size: 16px;
      margin-right: 24px;
    }
  }

  .tabs__nav-item:not(:last-child) {
    margin-bottom: 16px;

    @include tablet {
      margin-bottom: 0;
      margin-right: 24px;
    }

    @include mobile {
      margin-right: 16px;
    }
  }

  .tabs__nav-btn {
    font-weight: 500;
    font-size: 20px;
    line-height: 121.9%;
    color: var(--grey-400);
    background-color: transparent;
    border-bottom: 2px solid var(--base-grey);
    transition: color 0.5s ease, border-bottom 0.5s ease;

    &:hover {
      color: var(--cyan-400);
    }

    @include tablet {
      padding: 16px 0;
      font-weight: 600;
      font-size: 16px;
    }
  }

  .tabs__panel--active {
    display: block;
  }

  .tabs__nav-btn--active {
    color: var(--base-white);
    border-bottom: 2px solid var(--cyan-400);

    &:hover {
      color: var(--base-white);
    }
  }

  .header__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: auto;
    gap: 24px;
    margin-bottom: 0;
  }

  .header__link {
    display: flex;
    padding: 12px;
  }

  .header__card {
    border-radius: 12px;
    border: 2px solid var(--base-grey);
    transition: border 0.3s ease-in;

    &:hover {
      border: 2px solid var(--cyan-400);
    }

    &-icon {
      display: block;
      min-width: 32px;
      height: 32px;
      margin-right: 12px;
    }

    &-title {
      font-weight: 600;
      font-size: 20px;
      line-height: 140%;
      color: var(--base-white);
      margin-bottom: 4px;
    }

    &-descr {
      font-weight: 400;
      font-size: 14px;
      line-height: 143%;
      color: var(--grey-400);
    }
  }

  .viewall-btn {
    margin-top: 24px;
    a {
      padding: 8px 12px;
      color: var(--base-white);
    }

    &:hover {
      a {
        color: var(--cyan-400);
      }
    }
  }

  @include small-tablet {
    display: none;
  }
}

// Поиск

header .search-bar-container {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  padding: 15px 15px;
  background: var(--base-grey);
  display: flex;
  align-items: center;
  z-index: 1001;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: clip-path 0.2s ease-in-out;
}

header .search-bar-container.active {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

header .search-bar-container #search-bar {
  width: 100%;
  padding: 12px 15px;
  text-transform: none;
  color: var(--base-black);
  font-size: 16px;
  border-radius: 6px;
  border: none;
}

header .search-bar-container label {
  color: #fff;
  cursor: pointer;
  font-size: 30px;
  margin-left: 20px;
}
