.product-about {
  padding-top: 48px;
  padding-bottom: 48px;
  border-bottom: 1px solid var(--grey-100);

  @include small-tablet {
    padding-top: 32px;
    padding-bottom: 32px;
  }

  &-download {
    padding-top: 24px;
    padding-bottom: 0;

    @include mobile {
      padding-top: 32px;
      padding-bottom: 0;
    }
  }

  &__title {
    margin-bottom: 12px;
  }

  &__descr {
    max-width: 840px;
    margin-bottom: 48px;

    @include small-tablet {
      margin-bottom: 32px;
    }

    @include mobile {
      font-size: 16px;
    }
  }

  &-download__descr {
    margin-bottom: 24px;

    @include medium-tablet {
      margin-bottom: 12px;
    }

    @include mobile {
      margin-bottom: 4px;
      font-size: 16px;
    }
  }


  &__btn {
    margin-bottom: 62px;

    @include tablet {
      margin-bottom: 40px;
    }

    @include small-tablet {
      margin-bottom: 24px;
    }

    @include mobile {
      margin-bottom: 18px;
    }
  }

  .link {
    color: var(--blue-400);
    transition: color 0.3s ease-in;
    i {
      color: var(--blue-400);
      transition: color 0.3s ease-in;
    }

    &:hover {
      color: var(--cyan-400);
      i {
        color: var(--cyan-400);
      }
    }
  }

  &__img {
    max-width: 1076px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 16px;

    @include mobile {
      border-radius: 12px;
    }

    img {
      display: block;
      height: 100%;
      border-radius: 16px;

      @include mobile {
        border-radius: 12px;
      }
    }
  }

  &-download__img {
    border-radius: 0;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;

    @include mobile {
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
    }

    img {
      border-radius: 0;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;

      @include mobile {
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
      }
    }
  }

  &__img-shadow {
    box-shadow: var(--shadow-3);
  }
}
