.article {
  padding-top: 48px;
  padding-bottom: 72px;

  @include medium-tablet {
    padding-bottom: 56px;
  }

  @include small-tablet {
    padding-top: 32px;
  }

  @include mobile {
    padding-bottom: 46px;
  }

  &__container {
    max-width: 1076px;
    margin: 0 auto;
  }

  &__info {
    display: flex;
    margin-bottom: 12px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  .blog-post__date {
    flex: 0 0 auto;
    margin-top: 13px;

    @include small-tablet {
      margin-top: 0;
      margin-bottom: 10px;
    }
  }

  .blog-post__tags {
    margin-top: 0;
    margin-left: 24px;

    @include small-tablet {
      margin-left: 0;
    }
  }

  .title1 {
    margin-bottom: 32px;
    @include small-tablet {
      margin-bottom: 24px;
    }
    @media (max-width: 420px) {
      font-size: 26px;
    }
  }

  &__img {
    width: 100%;
    height: auto;
    border-radius: 24px;
    margin-top: 32px;
    margin-bottom: 32px;

    @include mobile {
      border-radius: 15px;
      margin-top: 24px;
      margin-bottom: 24px;
    }
  }

  .content p {
    font-weight: 400;
    font-size: 18px;
    line-height: 156%;
    color: var(--grey-800);

    @include mobile {
      font-size: 16px;
    }
  }
}
